<template>
    <v-container>

        <v-row>
            <v-col cols="12">
                <h2>{{yourLanguagesText}}</h2>
            </v-col>
        </v-row>

        <v-col cols="12">
            <v-card>
                <v-progress-linear
                        value="50"
                        height="10"
                        color="blue-grey"
                        reactive
                        dark
                >
                </v-progress-linear>
                <v-container>
                    <YourLanguagesSelector
                            @dataValidityUpdate="isDataValid($event)">
                    </YourLanguagesSelector>

                    <v-btn block color="primary" @click="setUserLanguages" :disabled="buttonIsDisabled">
                        {{continueText}}
                    </v-btn>
                </v-container>
            </v-card>
        </v-col>
    </v-container>
</template>

<script>
    import YourLanguagesSelector from "../AuxInputComponents/YourLanguagesSelector";
    import Constants from "../../store/Constants";
    import {mapActions} from "vuex";

    export default {
        name: "FillYourLanguages",
        data() {
            return {
                buttonIsDisabled: true,
                knownLanguages: null,
                languagesToLearn: null,
                yourLanguagesText: Constants.TEXT_YOUR_LANGUAGES,
                continueText: Constants.TEXT_TO_CONTINUE,
            }
        },
        methods: {
            ...mapActions('user', ['setLanguages']),
            isDataValid: function (data) {
                if (data.isValid) {
                    this.languagesToLearn = data.languagesToLearn;
                    this.knownLanguages = data.knownLanguages;
                    this.buttonIsDisabled = false;
                } else {
                    this.buttonIsDisabled = true;
                }
            },
            setUserLanguages() {
                //Set languages to user profile
                let resultSetLanguages = this.setLanguages(
                    {
                        knownLanguages: this.knownLanguages,
                        languagesToLearn: this.languagesToLearn
                    });

                resultSetLanguages.then(() => {
                    this.$router.replace('set-your-profile-image');
                })
                    .catch((error) => {
                        this.snackbarText = error;
                        this.snackbar = true;
                    });
            },
        },
        components: {
            YourLanguagesSelector
        }
    }
</script>

